import { useEffect, useState } from "react";
import { connect }             from "react-redux";

//Our Imports
import { bindAll }      from "Helpers";
import GenericSerivices from "Store/Services/GenericSerivices";
import PreferencesPage  from "./PreferencesPage";

const PreferencesPageContainer = ({
	genericSerivices,
}) => {

	const [active, setActive] = useState("1");

	const toggle = tab => {
		if (active !== tab) {
			setActive(tab);
		}
	};

	useEffect(() => {
		genericSerivices.getAll({ module : "credit-grantors", actionType : "getAllCredit" });
	}, []);

	return (
		<PreferencesPage
			delegations={{
				active,
				toggle,
			}}
		/>
	);
};
const mapDispatchToProps = bindAll({ GenericSerivices });

export default connect(null, mapDispatchToProps)(PreferencesPageContainer);
