import { connect }             from "react-redux";
import { Card, Table, Button } from "reactstrap";
import { Edit, Trash }         from "react-feather";

//Our Imports
import { bindAll, isValidArray } from "Helpers";
import { ModalSlice }            from "Store/Slices";
import UserImage                 from "Components/Global/UserImage";
import Paginate                  from "Components/Global/Paginate";
import "./TablePreferences.scss";

const TablePreferences = ({
	credits,
	totalPages,
	modalSliceActions,
}) => {
	return (
		<Card id="TablePreferences">
			<Table responsive>
				<thead>
					<tr>
						<th className="title">
							Clave
						</th>
						<th className="title">
							otorgante
						</th>
						<th className="title">
							descripción
						</th>
						<th className="title last-column">
							Acciones
						</th>
					</tr>
				</thead>
				<tbody>
					{
						isValidArray(credits) &&
						credits.map(credit => (
							<tr key={credit?._id}>
								<td>
									<p className="key-text">{credit?.key}</p>
								</td>
								<td>
									<div className="gruop">
										<UserImage
											rounded
											size={33}
											image={credit?.image}
											name="bbva"
										/>
										<span>{credit?.name}</span>
									</div>
								</td>
								<td>
									<div>
										<p>
											{credit?.description ? credit.description : "N/A"}
										</p>
									</div>
								</td>
								<td>
									<div className="actions-wrapper">
										<Button.Ripple
											color='flat-secondary'
											className="btn-icon rounded-circle"
											onClick={ () => modalSliceActions.openModal({
												type  : "addPreferencesModal",
												props : credit,
											})}
										>
											<Edit className="edit-icon" size={16} />
										</Button.Ripple>
										<Button.Ripple
											color='flat-danger'
											className="btn-icon rounded-circle"
											onClick={ () => modalSliceActions.openModal({
												type  : "confirmationModal",
												props : {
													image                : "",
													_id                  : credit?._id,
													identifier           : credit?.name,
													name                 : "Otorgante de crédito",
													actionToDispatch     : "deletCredit",
													title                : "Eliminar Otorgante de crédito",
													confirmationQuestion : "¿Estás seguro que quieres eliminar este otorgante de crédito?",
													bodyText             : "Este otorgante de crédito se eliminará permanentemente.",
													confirmLabel         : "Sí, Eliminar",
												},
											})}
										>
											<Trash size={16} />
										</Button.Ripple>
									</div>
								</td>
							</tr>
						))
					}
				</tbody>
			</Table>
			<Paginate
				module="credit-grantors"
				totalPage={totalPages}
				actionType="getAllCredit"
			/>
		</Card>
	);
};
const mapDispatchToState = ({ _creditgrantorsSlice }) => ({
	credits    : _creditgrantorsSlice?.docs       ?? [],
	totalPages : _creditgrantorsSlice?.totalPages ?? 1,
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});


export default connect(mapDispatchToState, mapDispatchToProps)(TablePreferences);
