import { connect } from "react-redux";
import { Button }  from "reactstrap";
import { Plus }    from "react-feather";

//Our Imports
import { bindAll }    from "Helpers";
import { ModalSlice } from "Store/Slices";
import "./AddPreferencesBtn.scss";

const AddPreferencesBtn = ({
	modalSliceActions,
}) => {
	return (
		<div id="AddPreferencesBtn">
			<h1 className="title">Otorgantes de Crédito</h1>
			<p className="text">
				Lista de entidades y empresas registradas com
				Otorgantes de Crédito
			</p>
			<Button.Ripple
				size="sm"
				className="btn-style"
				color="flat-primary"
				onClick={() => modalSliceActions.openModal({ type : "addPreferencesModal" })}
			>
				<span>Agregar Otorgante</span>
				<Plus  size={14} />
			</Button.Ripple>
		</div>
	);
};
const mapDispatchToProps = bindAll({ ModalSliceActions : ModalSlice.actions});

export default connect(null, mapDispatchToProps)(AddPreferencesBtn);
