import {
	Nav,
	Card,
	NavLink,
	TabPane,
	NavItem,
	TabContent,
} from "reactstrap";

//Our Imports
import AddPreferencesBtn from "Components/Preferences/AddPreferencesBtn";
import TablePreferences  from "Components/Preferences/TablePreferences";
import "./PreferencesPage.scss";

const PreferencesPage = ({
	delegations : {
		active,
		toggle,
	},
}) => {

	return (
		<div id="PreferencesPage">
			<Card>
				<Nav tabs fill>
					<NavItem>
						<NavLink
							className="first-nav"
							active={active === "1"}
							onClick={() => toggle("1")}
						>
							Catálogos
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={active === "2"}
							onClick={() => toggle("2")}
						>
							Parámetro 2
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={active === "3"}
							onClick={() => toggle("3")}
						>
							Parámetro 3
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={active === "4"}
							onClick={() => toggle("4")}
						>
							Parámetro 4
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={active === "5"}
							onClick={() => toggle("5")}
						>
							Parámetro 5
						</NavLink>
					</NavItem>
					<NavItem>
						<NavLink
							active={active === "6"}
							onClick={() => toggle("6")}
						>
							Parámetro 6
						</NavLink>
					</NavItem>
				</Nav>
			</Card>
			<hr />
			<TabContent className='py-50' activeTab={active}>
				<TabPane tabId='1'>
					<div className="table-tab">
						<AddPreferencesBtn />
						<TablePreferences />
					</div>
				</TabPane>
				<TabPane tabId='2'>
				</TabPane>
				<TabPane tabId='3'>
				</TabPane>
				<TabPane tabId='4'>
				</TabPane>
			</TabContent>
		</div>
	);
};

export default PreferencesPage;
